 /*EO Find a better way to override components default css than adding !important*/

 body {
	font: "Lucida Grande", Helvetica, Arial, sans-serif;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.background {
	background-image: url('../images/wallpaper.jpg');
	background-size: 100% 100%;
	background-color: #4682B4;
}

.jumbotron {
	margin: 50px;
	padding: 2rem !important;
}

/*  Class Color for jsTree status */
.NotForm{
  color:HotPink;
}

.NotUpload{
  color:Orange;
}

.NotBoth{
  color: red;
}

.Ongoing{
  color : orange
}

.WaitAdjudication{
  color : red
}

.Done{
  color : green
}

/*Css for regular element*/

label{
  vertical-align: middle;
}

.bloc_bordures{
  padding: 5px;
  border: 1px #ccc solid;
}

.bloc_bordures_orange {
  color: #ff9900;
  padding: 5px;
  border: 1px #ff9900 solid;
}

.icon{
  width: 20px;
}

h1{
  color: black;
  margin: 20px 0 12px 0;
  font-family: SourceSansProBold;
  font-variant-caps: petite-caps;
}

.table{
font-size: 14px;
}

/* Mettre une bordure sur les td et les th */
.table-border td,
.table-border th {
    border: 1px solid rgba(0,0,0,0.2);
    padding: 5px;
    text-align: center;
}

.column_search {
  border: solid 1px #ccc;
  border-radius: 4px;
}



.w-100 { max-width: 100px; }
.w-200 { max-width: 200px; }
.w-300 { max-width: 300px; }
.w-400 { max-width: 400px; }
.w-500 { max-width: 500px; }
.w-600 { max-width: 600px; }
.w-700 { max-width: 700px; }


.main-content {
	padding: 12px 64px;
	background-color: white;
	box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
}
.container {
	background-color: ghostwhite !important;
	max-width: 90% !important;
	margin-top: 10%;
}
.block {
	margin: 24px auto;
	background-color: ghostwhite !important;
	box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
	border-radius: 8px !important;
}
.block-400 {
	max-width: 400px;
}
.block-500 {
	max-width: 500px;
}
.block-title {
	padding: 8px 18px;
	text-align: center;
	font-family: SourceSansPro;
	font-size: 24px !important;
	color: white;
	background-color: rgb(37, 118, 144, 0.8);
  	box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
  	border-radius: 8px 8px 0 0;
}
.block-content {
	padding: 32px !important;
}
.block-panel {
	margin: auto 16px;
	flex: 1;
}
.block-panel-title {
	color: #1F5EA7;
	font-size: 16px;
	font-family: SourceSansProBold;
}


.btn {
	margin: 0.2rem;
}

.visitLink {
	text-decoration: underline;
}

.tree {
	margin-top: 16px;
}

.tree-aside {
	overflow: auto;
	max-height: 900px;
  border-right: 2px solid #863569;
}

.dropdown {
	display: inline-block;
	width: 150px;
	height: auto;
	padding:3px 4px 4px;
	flex:1;
}

.col {
	flex:1;
}

.href {
	font-size: 1rem;
	color: #007bff;
	cursor: pointer;
}

.href:visited {
	color:hotpink
}

.href:hover {
	text-decoration: underline;
}


/* Admin Tab */
.admin-tab {
	display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
	padding: 10px;
	margin-bottom: 30px !important;
}

.tab {
	background-color: #fff;
}

.card {
	background-color: #fff !important;
	margin-top: 4rem;
}


.navbar-pad {
	padding-left: 200px;
}

.nav-link.active {
	background-color: lightblue !important;
}


/* Specifics */
#logos #logo-gaelo {
	display: block;
	margin: 24px auto 0 auto;
	height: auto;
	max-height: 180px;
	width: auto;
}

#logos #name-div {
	text-align: center;
	font-size: 38px;
	font-family: SlaboRegular;
	font-weight: bold;
	color: #007bff;
	padding:0;
	width:100%;
}

#login label {
	margin-bottom: 0;
}

#login fieldset {
	margin-bottom: 12px;
}

#login .connexionBtn {
	width: 150px;
}

/* Visit builder */
.vbuild .deleted {
	color: #950e0e;
	background-color: #ffdbdb;
	font-style: italic;
}
.vbuild .modified {
	color: #0f192c;
	background-color: #ccebff;
	font-style: italic;
}
.vbuild .added {
	color: #224627;
	background-color: #befdbd;
	font-style: italic;
}

/* Static */
#header {
	height: 44px;
}

#header #header-content {
	position: fixed;
	z-index: 100;
	width: 100%;
	height: 44px;
	background-color:#007bff;
}

.header-bg-color {
	background-color: #007bff
	;
}

#logo-gaelo {
	height: 44px;
}

#name-div {
	display: inline-block;
	padding: 3px 0 0 20px;
	font-size: 28px;
	font-family: SlaboRegular;
	color: #EDEFF3;
}

.authentication {
	background-image: url('../images/splash-screen.png');
	padding-bottom: 0px !important;
	background-size: 100% 100%;

}